.reviews__item{
    display: grid;
    grid-template-columns: rem(260px) 1fr;
    padding: rem(24px);
    background: #F1F1F3;
    grid-gap: rem(37px);
    margin-bottom: rem(4px);

    @media(max-width: 768px){
        grid-template-columns: 100%;
        grid-gap: 16px;
        padding: 16px;
    }
    &-images{
        display: flex;
        align-items: center;
        gap: rem(16px);
        margin-top: rem(16px);
        a{
            width: rem(56px);
            height: rem(56px);
            display: block;
            overflow: hidden;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }


    .stars{
        margin-bottom: rem(6px);
        @media(max-width: 768px){
            .star{
                display: block;
                width: 18px;
                height: 18px;
                margin-right: 5px;
            }
        }
    }

    &-author-name{
        font-weight: 600;
        font-size: rem(18px);
        line-height: rem(32px);
        color: #2D2727;
        @media(max-width: 768px){
            font-weight: 600;
            font-size: 12px;
            line-height: 24px;
        }
    }

    &-author{
        @media(max-width: 768px){
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            max-height: 50px;
        }
    }

    &-date{
        font-weight: 400;
        font-size: rem(14px);
        line-height: rem(24px);
        color: rgba(45, 39, 39, 0.7);
        @media(max-width: 768px){
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            margin-top: 3px;
        }
    }

    &-content{
        .buy-here{
            padding: rem(6px) rem(16px);
            background: #FFFFFF;
            border-radius: rem(16px);
            font-weight: 500;
            font-size: rem(16px);
            line-height: rem(28px);
            display: flex;
            align-items: center;
            max-width: max-content;
            .green-mark{
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.70862 7.29441C2.31894 6.90305 1.68578 6.90169 1.29441 7.29138C0.903052 7.68106 0.901691 8.31422 1.29138 8.70559L2.70862 7.29441ZM5.98276 11.9999L5.27413 12.7055C5.46153 12.8937 5.71609 12.9996 5.98168 12.9999C6.24727 13.0002 6.50206 12.8948 6.68986 12.707L5.98276 11.9999ZM14.6899 4.70711C15.0804 4.31659 15.0804 3.68342 14.6899 3.2929C14.2993 2.90237 13.6662 2.90237 13.2757 3.29289L14.6899 4.70711ZM1.29138 8.70559L5.27413 12.7055L6.69138 11.2943L2.70862 7.29441L1.29138 8.70559ZM6.68986 12.707L14.6899 4.70711L13.2757 3.29289L5.27565 11.2928L6.68986 12.707Z' fill='%2366CE7D'/%3E%3C/svg%3E%0A");
                width:rem(16px);
                height: rem(16px);
                background-size: cover;
                background-position: center;
                display: block;
                margin-right: rem(8px);
                @media(max-width: 768px){
                    width: 8px;
                    height: 8px;
                    margin-right: 8px;
                }
            }
            margin-bottom: 16px;
            @media(max-width: 768px){
                font-weight: 500;
                font-size: 12px;
                line-height: 24px;
                padding: 0px 8px;
                margin-bottom: 8px;
            }
        }

        p{
            font-weight: 400;
            font-size: rem(14px);
            line-height: rem(24px);
            color: #2D2727;
            @media(max-width: 768px){
                font-weight: 400;
                font-size: 12px;
                line-height: 18px;
            }


        }
    }


}